import api from "../api";
import { endPoint } from "../endPoint";
import { Config } from "../config";
import { authHeaders, clearLocalStorage } from "../utils";
import router from "../../router";
import i18n from "@/plugins/i18n";
import Vue from "vue";
import { showApiError } from "@/shared/utils";
const errorHandler = (error) => {
  console.log("error",error)
  if (!error.response) {
    Vue.swal
      ({
        type: "warning",
        text: i18n.t("NETWORKERROR"),
      })
  } 
 else if (error.request.status === 401) {
    Vue.swal
      .fire({
        type: "error",
        text: i18n.t("UNAUTHORISED"),
        icon: "error",
        confirmButtonText: i18n.t("LOGIN"),
      })
      .then((res) => {
        if (res) {
          clearLocalStorage.clear();
          router.push("/login");
        }
      });
      
  }
  return error;
};

const successHandler = (response) => {
  let url=endPoint.MLOGIN
  let url2=endPoint.LOGIN
  let usermanagement = endPoint.EVALUATIONUSERMANAGEMENT
  let forgotPassword = endPoint.FORGOTPASSWORD
  let mforgotPassword = endPoint.MFORGOTPASSWORD
  let clientcostexpansionitemlist=endPoint.EVALUATIONCLIENTCOSTEXPANSIONITEMLIST
  let readclientpointinfo=endPoint.CLIENTREADCLIENTPOINTINFO
  let dashboard=endPoint.DASHBOARD
  let clientDetailsUrl = endPoint.INTEGRATIONCLIENTDETAILS
  let readjournalstatus = endPoint.INTEGRATIONREADJOURNALSTATUS
  let evalutionExapanseComp = endPoint.EVALUATIONEXPENSEITEMCOMPAISON
  let integrationRefreshToken = endPoint.INTEGRATIONREFRESHTOKEN
  let dashboardCrfCostInfoTree = endPoint.EVALUATIONCOSTINFOTREELIST
  let improvementExpenseComp = endPoint.EVALUATIONCOMPANYJUDGEMENTCOMPARISON
  let evaluationExpenseComparison = endPoint.EVALUATIONEXPENSEITEMCOMPAISON
  let evaluationReadExpensisYear = endPoint.EVALUATIONREADEXPENSISYEAR
  let evaluationReadYearlyCostInfo = endPoint.EVALUATIONREADYEARLYCOSTINFO
  if (
    (response.request.status != 200 || !response.data.success) &&
      response.config.url != `${Config.apiUrl}${url}` &&
      response.config.url != `${Config.apiUrl}${url2}` &&
      response.config.url != `${Config.apiUrl}${forgotPassword}` &&
      response.config.url != `${Config.apiUrl}${mforgotPassword}` &&
      response.config.url != `${Config.apiUrl}${usermanagement}` &&
      response.config.url != `${Config.apiUrl}${clientcostexpansionitemlist}` &&
      response.config.url != `${Config.apiUrl}${readclientpointinfo}` &&
      response.config.url != `${Config.apiUrl}${dashboard}` &&
      response.config.url != `${Config.apiUrl}${clientDetailsUrl}` &&
      response.config.url != `${Config.apiUrl}${readjournalstatus}` &&
      response.config.url != `${Config.apiUrl}${evalutionExapanseComp}` &&
      response.config.url != `${Config.apiUrl}${integrationRefreshToken}` &&
      response.config.url != `${Config.apiUrl}${improvementExpenseComp}` &&
      response.config.url != `${Config.apiUrl}${evaluationExpenseComparison}` &&
      response.config.url != `${Config.apiUrl}${evaluationReadExpensisYear}` &&
      response.config.url != `${Config.apiUrl}${evaluationReadYearlyCostInfo}` &&
      response.config.url != `${Config.apiUrl}${dashboardCrfCostInfoTree}` 
  ) {
    showApiError(i18n.t("ERRORMESSAGE"));
  }
  return response;
};
api.interceptors.response.use(
  (response) => successHandler(response),
  (error) => errorHandler(error)
);
export default {
  getGroupTypes(payload) {
    return api.post(`${endPoint.GROUPTYPESLIST}`, payload);
  },

  postAPI(url, payload, auth = false) {
    let headers = {};
    if (auth) {
      headers = authHeaders();
    }
    return api.post(`${Config.apiUrl}${url}`, payload, { headers: headers });
  },
  getAPI(url, payload, auth = false) {
    let headers = {};
    if (auth) {
      headers = authHeaders();
    }

    return api.get(`${Config.apiUrl}${url}`, {
      params: payload,
      headers: headers,
    });
  },
  getCourses(payload, auth = false) {
    let headers = {};
    if (auth) {
      headers = authHeaders();
    }
    return api.get(`${endPoint.COURSE}`, {
      params: payload,
      headers: headers,
    });
  },
  putAPI(url, payload, auth = false) {
    let headers = {};
    if (auth) {
      headers = authHeaders();
    }
    return api.put(`${Config.apiUrl}${url}`, payload, { headers: headers });
  },
  deleteAPI(url, payload, auth = false) {
    let headers = {};
    if (auth) {
      headers = authHeaders();
    }

    return api.delete(`${Config.apiUrl}${url}`, {
      params: payload,
      headers: headers,
    });
  },
};
